@import url("https://api.fontshare.com/v2/css?f[]=switzer@300,500,700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=DM+Serif+Display:ital@0;1&display=swap");

body#suttas-body {
    background-color: #1c1c1c;
    color: #ccc;
    font-family: Switzer, Helvetica, Arial, sans-serif;
    margin: 0 15px;
    text-align: center;

    a,
    button,
    select {
        color: #eeb54d;

        &:hover {
            cursor: pointer;
            opacity: 0.8;
        }
    }

    #blurb,
    h1,
    h2 {
        font-family: "DM Serif Display", Georgia, "Times New Roman", Times,
            serif;
    }

    #blurb {
        color: #dab87a;
        font-style: italic;
    }

    button,
    input,
    select {
        appearance: none;
        background-color: #1c1c1c;
        border: 0;
        border-radius: 5px;
        color: #eeb54d;
        font-size: 1rem;
        padding: 5px 8px;
    }

    h2,
    #subheader,
    #sutta-text table td:first-child,
    p.copyright {
        color: #888;
    }

    footer,
    header {
        p {
            margin: revert;
        }
    }

    footer {
        color: #666;
        margin: 50px 0;

        .admin-btn {
            background-color: transparent;
            border: 0;
            padding: 0;
            text-decoration: underline;
        }
    }

    h2 {
        font-weight: 400;
    }

    header {
        margin: 60px 0 40px;
        text-align: center;

        h1 {
            margin: 0;
        }

        #subheader {
            font-family: "DM Serif Display", Georgia, "Times New Roman", Times,
                serif;
            font-size: 1.25rem;
            margin: 10px 0;
        }
    }

    hr {
        max-width: 400px;
        background-color: #444;
    }

    main {
        width: 100%;
        max-width: 700px;
        background-color: #2b2621;
        border-radius: 20px;
        box-sizing: border-box;
        margin: auto;
        padding: 15px;

        h2.canon-name {
            font-family: auto;
            font-size: 1.2rem;
            font-style: italic;
            font-weight: bold;
        }

        #sutta-text {
            table {
                text-align: left;

                td:first-child {
                    font-size: 0.8rem;
                    text-align: right;
                    padding-right: 10px;
                    vertical-align: top;
                }
            }
        }
    }

    p {
        &.copyright {
            font-size: 0.9rem;
        }

        &:not(.copyright):has(+ .copyright) {
            margin-bottom: 20px;
        }
    }
}
