body#admin-body {
    #post {
        #topic {
            width: 100%;
            height: 85px;
            box-sizing: border-box;
        }

        div[contenteditable="true"] {
            width: 100%;
            box-sizing: border-box;
            overflow: auto;
            text-align: left;

            &#post-content {
                height: 500px;
            }

            &#tweet-content {
                height: 150px;
            }

            .revision {
                &:hover {
                    cursor: pointer;
                    opacity: 0.8;
                    text-decoration: underline lightsalmon;
                }

                s {
                    color: lightsalmon;
                    font-style: italic;
                }

                b {
                    color: lightgreen;
                }
            }
        }

        .char-count {
            font-weight: bold;

            &.valid {
                color: lightgreen;
            }

            &.invalid {
                color: lightsalmon;
            }
        }

        label.revise-label {
            span {
                text-align: left;
            }
        }

        .accept-reject {
            display: none;
        }

        #post-meta-data {
            button {
                width: fit-content;
            }

            input {
                width: 100%;
                box-sizing: border-box;
            }
        }

        #draft-label {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 7px;
        }
    }
}
