/* UTILITY CLASSES */

& .flex {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px;

    &.column {
        flex-direction: column;
    }
}

/* END UTILITY CLASSES */

/* MULTI COMPONENT */

#select-table {
    & tbody {
        & tr:not(:last-child) {
            & td:first-child {
                text-align: right;
            }

            & select {
                width: 100%;
            }
        }
    }
}

button,
& div[contenteditable="true"],
h1,
h2,
h3,
h4,
h5,
h6,
hr,
input,
p,
select,
textarea {
    margin: 0;
}

hr {
    height: 2.75px;
    width: 100%;
    border: 0;
}

/* END MULTI COMPONENT */
